// import * as bear from '../../assets/images/bear.png';
export class ImageService {
    constructor() { }
    static get(imageName) {
        const img = new Image();
        img.src = this.baseImagesUrl + imageName;
        return img;
    }
    static bear() {
        const img = new Image();
        img.src = 'data:image/png;base64,';
        return img;
    }
}
ImageService.baseImagesUrl = '/src/assets/images/';
